import "core-js/modules/es.array.push.js";
import { onMounted, reactive, toRefs, ref, watch } from '@vue/runtime-core';
import { instance_Search } from '../plugin/axiosInstance';
export default {
  props: {
    // activefindGfindFfindP
    active: String
  },
  setup(props, context) {
    // console.log('setup:', props);
    // console.log('context:', context);
    // 
    let activePage = reactive({
      findG: {
        isActive: false,
        // 
        ph: ' //',
        // 
        class: {
          bigRed: false
        }
      },
      findF: {
        isActive: false,
        // 
        ph: '/// ',
        class: {
          bigRed: false
        }
      },
      findP: {
        isActive: false,
        // 
        ph: '// 1688',
        class: {
          bigRed: false
        }
      }
    });
    // 
    let ph = ref('');
    // 
    let keyWord = ref('');
    // 
    let photoUrl = ref('https://img.alicdn.com/imgextra/i3/O1CN0116UWgv1ouZsMOIfA7_!!6000000005285-2-tps-60-52.png');
    // 
    let recent_keywords = reactive([]);
    // 
    let hot_keywords = reactive(['', '', '', '', '', '', '', '', '', '', '']);
    // ajaxaxiosInstance.js
    let factoryData = reactive([{
      name: '',
      imgUrl: 'https://gw.alicdn.com/imgextra/i1/O1CN01vPED2s1yxSbEv6ZRI_!!6000000006645-2-tps-32-32.png',
      factorys: ['', '', '', '', '361', '', '', '', '']
    }, {
      name: '',
      imgUrl: 'https://gw.alicdn.com/imgextra/i3/O1CN01Q3YKsM1mcbAaaHlJD_!!6000000004975-2-tps-32-32.png',
      factorys: ['', '', '', '', '', '', '', '']
    }, {
      name: '',
      imgUrl: 'https://gw.alicdn.com/imgextra/i1/O1CN01c66ZWu1JfEalB63ue_!!6000000001055-2-tps-32-32.png',
      factorys: ['', '', '', '', '', '', '', '', '', '', '']
    }]);
    // 
    let isShowTips = ref(false);
    // 
    let ObjResult = reactive({
      lastKeyWord: '',
      results: []
    });
    // 
    let isShowResults = ref(false);

    // 
    function hoverPhoto() {
      photoUrl.value = 'https://img.alicdn.com/imgextra/i1/O1CN01d9tkR81gu6XZcEWvQ_!!6000000004201-2-tps-60-52.png';
    }
    // 
    function restorePhoto() {
      photoUrl.value = 'https://img.alicdn.com/imgextra/i3/O1CN0116UWgv1ouZsMOIfA7_!!6000000005285-2-tps-60-52.png';
    }
    // ajax()
    function sendKeyword(keyword) {
      keyword = keyword.trim(); // 
      // 
      if (keyword === '') {
        alert('');
        return;
      }
      // alert('1ajaxkeyword,;\n2keywordlocalStorage');
      //
      // 
      let isExit = false;
      let recentArr = JSON.parse(localStorage.getItem('Alibar-recentSearch-keyWord')) || [];
      recent_keywords.splice(0); // 
      for (let tmp of recentArr) {
        recent_keywords.push(tmp);
      }
      recent_keywords.forEach(element => {
        if (element === keyword) {
          isExit = true;
        }
      });
      // 
      if (isExit === true) {
        return;
      }
      // 
      recent_keywords.push(keyword);
      // 
      localStorage.setItem('Alibar-recentSearch-keyWord', JSON.stringify(recent_keywords));
    }
    // 
    function clickInput() {
      if (keyWord.value.length === 0) {
        // 
        isShowResults.value = false;
        isShowTips.value = true;
      } else {
        searchData();
      }
    }
    // 
    function hideTip_Result() {
      // console.log('');
      isShowTips.value = false;
      isShowResults.value = false;
    }
    // ajax
    let timer = null;
    // 0.7sajax
    function delaySearchData() {
      // console.log('timer:', timer);
      if (timer != null) {
        // console.log('timer != null');
        // 
        clearTimeout(timer);
        timer = null;
      }
      // ajax
      if (keyWord.value.length === 0) {
        return;
      }
      timer = setTimeout(() => {
        // ajax
        // console.log(':', keyWord.value);
        instance_Search({
          method: 'GET',
          url: '/Search/searchData',
          params: {
            key: keyWord.value
          }
        }).then(response => {
          // console.log(':', response.data);
          // 
          ObjResult.lastKeyWord = keyWord.value;
          // 
          ObjResult.results = [];
          let keyLength = keyWord.value.length;
          let arr = response.data;
          // 
          arr.forEach(element => {
            ObjResult.results.push(element.slice(keyLength));
          });
          // 
          isShowTips.value = false;
          // 
          isShowResults.value = true;
        }, reject => {
          // 
          // 
          isShowTips.value = false;
          // 
          isShowResults.value = false;
        });
      }, 700);
    }
    // ajaxajax
    function searchData() {
      if (timer != null) {
        // console.log('timer != null');
        // 
        clearTimeout(timer);
        timer = null;
      }
      // ajax
      if (keyWord.value.length === 0) {
        return;
      }
      instance_Search({
        method: 'GET',
        url: '/Search/searchData',
        params: {
          key: keyWord.value
        }
      }).then(response => {
        // console.log(':', response.data);
        // 
        ObjResult.lastKeyWord = keyWord.value;
        // 
        ObjResult.results = [];
        let keyLength = keyWord.value.length;
        let arr = response.data;
        // 
        arr.forEach(element => {
          ObjResult.results.push(element.slice(keyLength));
        });
        // 
        isShowTips.value = false;
        // 
        isShowResults.value = true;
      }, reject => {
        // 
        // 
        isShowTips.value = false;
        // 
        isShowResults.value = false;
      });
    }
    // 
    function delayBlur() {
      setTimeout(() => {
        hideTip_Result();
      }, 150);
    }
    // 
    function clickKey(keyword) {
      // console.log('!', keyword);
      keyWord.value = keyword;
      sendKeyword(keyword);
    }

    // 
    onMounted(() => {
      // 
      activePage[props.active].isActive = true;
      activePage[props.active].class.bigRed = true;
      // 
      ph.value = activePage[props.active].ph;
      // phNewMainBox

      // 
      let recentArr = JSON.parse(localStorage.getItem('Alibar-recentSearch-keyWord')) || [];
      for (let tmp of recentArr) {
        recent_keywords.push(tmp);
      }
    });
    return {
      ...toRefs(activePage),
      // 
      ph,
      //                 
      photoUrl,
      //                 
      hoverPhoto,
      //                 
      restorePhoto,
      //                 
      keyWord,
      //                 
      sendKeyword,
      //                 
      recent_keywords,
      // localStorage                
      hot_keywords,
      // 
      factoryData,
      // 
      clickInput,
      // 
      hideTip_Result,
      // 
      isShowTips,
      // 
      delaySearchData,
      // ajax
      searchData,
      // ajax
      ...toRefs(ObjResult),
      // 
      isShowResults,
      // 
      clickKey,
      // 
      delayBlur // 
    };
  }
};