import { reactive, ref, toRefs } from '@vue/reactivity';
import { computed, onBeforeMount, onMounted, onUnmounted } from '@vue/runtime-core';
export default {
  name: 'Sidebar',
  setup(props) {
    // 
    let bigSizeBar = reactive({
      mine: false,
      // 
      invite: false,
      // 
      update: false,
      // 
      track: false,
      // 
      message: false,
      // 
      headline: false,
      // 
      big_goTop: false // 
    });
    // visibility
    let bigSizebar_p_visibility = reactive({});
    // 
    let ele_bigSizeBar = ref();
    // ultop
    let ul_top = ref();
    // 
    let goTop_timer = null;
    // 
    let smallSizeBar = reactive({
      replenishment: false,
      // 
      cardCenter: false,
      // 
      purchaseList: false,
      // 
      feedback: false,
      // 
      plus: false,
      // 
      small_goTop: false // 
    });
    // : 
    let small_isShowGoTop = ref();

    // ultop
    function get_ul_top() {
      let h = document.documentElement.clientHeight; // 100vh
      if (h >= 753) {
        // ul407px,    top(100vh-407)/2
        ul_top.value = Math.ceil((h - 407) / 2).toString() + 'px';
      } else if (h < 753 && h >= 573) {
        //100vh573pxtop50pxul
        // top 100vh -  -  - ul/2  top  ul
        // console.log('', (h-50-16-407)/2);
        ul_top.value = Math.ceil((h - 50 - 16 - 407) / 2).toString() + 'px';
      } else {
        // bigSizebar543px = 20   + 407     +   50    +       16    +     50
        //                                   top  + ul   +     +  + 
        ul_top.value = h - 407 - 50 - 16 - 50;
        if (ul_top.value < 20) {
          ul_top.value = 20;
        }
        ul_top.value += 'px';
      }
    }
    onBeforeMount(() => {
      // console.log(':', document.documentElement.clientHeight);
      get_ul_top(); // bigSizebarul
      // console.log('ul_top:', ul_top.value);
      // bigSizebar_p_visibility
      for (let i in bigSizeBar) {
        bigSizebar_p_visibility[i] = computed(() => {
          return bigSizeBar[i] ? 'visible' : 'hidden';
        });
      }
      console.log(':', document.documentElement.scrollTop);
      if (document.documentElement.scrollTop === 0) {
        console.log('');
        small_isShowGoTop.value = false;
      }
      // console.log('1111');
      // console.log(bigSizebar_p_visibility);
      // for(let i in bigSizebar_p_visibility) {
      //     console.log(i, bigSizebar_p_visibility[i]);
      // }
    });

    onMounted(() => {
      // console.log(':', ele_bigSizeBar.value.offsetHeight);
      // 
      let bigSizebar_timer = null;
      function set_ul_top() {
        // 
        if (bigSizebar_timer != null) {
          clearTimeout(bigSizebar_timer);
          // console.log("");
        }

        bigSizebar_timer = setTimeout(() => {
          get_ul_top();
          bigSizebar_timer = null; // bigSizebar_timernull
          // console.log('');
        }, 500);
      }
      // ul
      window.addEventListener('resize', set_ul_top, false);
      function set_smallSizeBar_goTop() {
        if (small_isShowGoTop.value === false && document.documentElement.scrollTop > 0) {
          small_isShowGoTop.value = true;
        } else if (small_isShowGoTop.value === true && document.documentElement.scrollTop === 0) {
          small_isShowGoTop.value = false;
        }
      }
      // : 
      window.addEventListener('scroll', set_smallSizeBar_goTop, false);
    });

    // bigSizebar
    function big_mouseenter(name) {
      bigSizeBar[name] = true;
    }
    // bigSizebar
    function big_mouseleave(name) {
      bigSizeBar[name] = false;
    }
    // smallSizeBar
    function small_mouseenter(name) {
      smallSizeBar[name] = true;
    }
    // smallSizebar
    function small_mouseleave(name) {
      smallSizeBar[name] = false;
    }
    // : 
    function click_goToTop() {
      // 
      if (goTop_timer != null) {
        return;
      }
      let h = document.documentElement.scrollTop; // 
      // console.log(':', h);
      if (h === 0) {
        // 
        return;
      }
      let onceStep = Math.ceil(h / 30); // 0.3s0.01s0.3/0.01 = 30
      // console.log(':', onceStep);
      goTop_timer = setInterval(() => {
        if (h === 0) {
          clearInterval(goTop_timer);
          goTop_timer = null;
          return;
        }
        h -= onceStep;
        h = h < 0 ? 0 : h;
        window.scroll(0, h);
        // console.log('...');
      }, 10);
    }
    onUnmounted(() => {
      // window
      window.removeEventListener('resize', set_ul_top, false);
      window.removeEventListener('scroll', set_smallSizeBar_goTop, false);
    });
    return {
      ...toRefs(bigSizeBar),
      // 
      bigSizebar_p_visibility,
      // visibility
      ele_bigSizeBar,
      // 
      ul_top,
      // ultop
      big_mouseenter,
      big_mouseleave,
      click_goToTop,
      ...toRefs(smallSizeBar),
      //  
      small_isShowGoTop,
      // : 
      small_mouseenter,
      small_mouseleave
    };
  }
};