    // [shrink]
    let isShrinkObj = reactive({
      isShrink: true,
      class: {
        smallBox: true
      }
    });
    //  
    function numToStr(num) {
      if (typeof num !== 'number') {
        // number
        return '00';
      }
      let k = parseInt(num);
      if (k < 10) {
        return '0' + k;
      }
      return k.toString();
    }
    // 
    function getCountdown(time) {
      let nowDate = new Date();
      let endDate = new Date(time);
      countdown.value = parseInt((endDate.getTime() - nowDate.getTime()) / 1000); // 
      if (countdown.value < 0) {
        // 
        countdown.value = 0;
      }
    }

    // 
    let endHour = computed(() => {
      return numToStr(parseInt(countdown.value / 60 / 60) % 24);
    });
    let endMinute = computed(() => {
      return numToStr(parseInt(countdown.value / 60) % 60);
    });
    let endSecond = computed(() => {
      return numToStr(countdown.value % 60);
    });
    let timer_countdown = null;

    // 
    function clickShrinkBtn() {
      isShrinkObj.isShrink = !isShrinkObj.isShrink;
      isShrinkObj.class.smallBox = !isShrinkObj.class.smallBox;
    }
    // span
    function clickA(obj) {
      for (let i in recOffer_arr) {
        recOffer_arr[i].active = false;
        recOffer_arr[i].class['active_a'] = false;
      }
      obj.active = true;
      obj.class['active_a'] = true;
    }

    // 
    function getDecimals(number) {
      if (parseInt(number) < number) {
        let num_arr = number.toString().split('.');
        if (num_arr.length > 1) {
          return '.' + num_arr[1];
        } else {
          return '';
        }
      }
    }
    // 
    function product_mouseEnter(obj) {
      obj.isHover = true;
    }
    // 
    function product_mouseLeave(obj) {
      obj.isHover = false;
    }
    onMounted(() => {
      // countdown
      getCountdown(endTime);
      timer_countdown = setInterval(() => {
        countdown.value -= 1;
        if (countdown.value < 0) {
          countdown.value = 0;
          clearInterval(timer_countdown);
        }
      }, 1000);

      // console.log(':', recOffer_arr.length);
      // 
      if (recOffer_arr.length > 0) {
        recOffer_arr[0].active = true;
        recOffer_arr[0].class['active_a'] = true;
      }
    });
    return {
      baoping_arr,
      // 
      endHour,
      // _
      endMinute,
      // _
      endSecond,
      // _
      yanxuan_arr,
      // 1688
      yijian_arr,
      // 
      recOffer_arr,
      // 
      isShrinkObj,
      // 
      clickShrinkBtn,
      // 
      clickA,
      // span
      getDecimals,
      // 
      product_mouseEnter,
      // 
      product_mouseLeave // 
