    function myAnimate(first_ele, first_target, second_ele, second_target, time) {
      let first_moveStep = first_target - parseFloat(first_ele.style.left); // 
      let first_onceStep = first_moveStep / k; // 
      let second_moveStep = second_target - parseFloat(second_ele.style.left); // 
      let second_onceStep = second_moveStep / k; //       
      let onceTime = time / k;
      let timer = setInterval(() => {
        // 
        let i = parseFloat(first_ele.style.left);
        i += first_onceStep;
        //    / k i
        if (first_onceStep > 0 && i > first_target || first_onceStep < 0 && i < first_target) {
          i = first_target;
        }
        first_ele.style.left = i + 'px';
        // 
        let t = parseFloat(second_ele.style.left);
        t += second_onceStep;
        //    / k  t
        if (second_onceStep > 0 && t > second_target || second_onceStep < 0 && t < second_target) {
          t = second_target;
        }
        second_ele.style.left = t + 'px';
        // 
        if (i === first_target && t === second_target) {
          clearInterval(timer);
          moving = false;
          // 
          if (timer_auto != null && flag === false) {
            clearInterval(timer_auto);
            timer_auto = null;
          }
        }
      }, onceTime);
    }
